import React, {Component} from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import BookService from "../services/BookService";
import {useHistory, withRouter} from "react-router-dom";

class ListBookComponent extends Component {

    constructor(props) {
        super(props);

        this.state = {
            books: [],
            MasterChecked: false,
            SelectedList: [],
            id: '',
            author: ''

        }
    };


    initIdHandler = (event) => {
        this.setState({id: event.target.value});
    }

    initAuthorHandler = (event) => {
        this.setState({author: event.target.value});
    }

    rowhandler = (selectedRow) => {
        this.state.id = ("selectedRow:", selectedRow)
        return this.updateBookById()
    }

    // присваиваем значения состоянию компонента
    componentDidMount() {
        BookService.getBooks().then((res) => {
            this.setState({books: res.data});
        });
    }

    onMasterCheck(e) {
        let tempList = this.state.books;
        // Check/ UnCheck All Items
        tempList.map((book) => (book.selected = e.target.checked));
        this.setState({
            MasterChecked: e.target.checked,
            books: tempList,
            SelectedList: this.state.books.filter((e) => e.selected),
        });
    }



    onItemCheck(e, item) {
        let tempList = this.state.books;
        tempList.map((book) => {
            if (book.id === item.id) {
                book.selected = e.target.checked;
            }
            return book;
        });
        const totalItems = this.state.books.length;
        const totalCheckedItems = tempList.filter((e) => e.selected).length;
        this.setState({
            MasterChecked: totalItems === totalCheckedItems,
            books: tempList,
            SelectedList: this.state.books.filter((e) => e.selected),
        });
    }
    getSelectedRows() {
        this.setState({
            SelectedList: this.state.books.filter((e) => e.selected),
        });
    }

    addBook(){

        this.props.history.push('/add-book');
        window.location.reload()
    }

    deleteBooks = async () => {
        this.SelectedList = this.state.SelectedList;
        const deleteRowIds = this.SelectedList.map(item => item.id);
        console.log('ids => ' + JSON.stringify(deleteRowIds));
        await BookService.deleteBook(deleteRowIds);
        window.location.reload()
    }
    searchBookById = (e) => {
        e.preventDefault();
        //this.id = this.state.id
        //console.log('id => ' + JSON.stringify(id));
        //BookService.setId(this.id);
        this.props.history.push({pathname:'/search-book-id/' + this.state.id});
        window.location.reload()
    }

    searchBookByAuthor = (e) => {
        e.preventDefault();
        //this.id = this.state.id
        //console.log('id => ' + JSON.stringify(id));
        //BookService.setId(this.id);
        this.props.history.push({pathname:'/search-book-author/' + this.state.author});
        window.location.reload()
    }



    updateBookById = (e) => {
        //e.preventDefault();
        //this.id = this.state.id
        //console.log('id => ' + JSON.stringify(id));
        //BookService.setId(this.id);
        this.props.history.push({pathname:'/update-book/'+this.state.id});
        window.location.reload()
    }

    render() {
        return (
            <div style={{marginBottom:'50px'}}>
                <div>
                <Form>
                        <Form.Label>ID</Form.Label>
                        {/*<Form.Control type="ID" placeholder="Enter ID" />*/}
                        <div class="input-group mb-3 w-25">
                        <input
                            placeholder="Идентификатор"
                            name="id"
                            className="form-control"
                            value={this.state.id}
                            onChange={this.initIdHandler}/>
                            <div style={{marginLeft:'5px'}}>
                            <button
                                className="btn btn-success"
                                onClick={this.searchBookById.bind(this)}>Найти
                            </button>
                            </div>
                        </div>
                    </Form>
                </div>
                <div>
                <Form>
                    <Form.Label>Author</Form.Label>
                    {/*<Form.Control type="ID" placeholder="Enter ID" />*/}
                    <div class="input-group mb-3 w-25">
                        <input
                            placeholder="Автор"
                            name="author"
                            className="form-control"
                            value={this.state.author}
                            onChange={this.initAuthorHandler}/>
                        <div style={{marginLeft:'5px'}}>
                            <button
                                className="btn btn-success"
                                onClick={this.searchBookByAuthor.bind(this)}>Найти
                            </button>
                        </div>
                    </div>
                </Form>
                </div>
                <h2 className="text-center"> Список книг </h2>
                <div className="row">
                    <table className="table table-striped table-bordered">
                        <thead>
                        <tr>
                            <th scope="col">
                                <input
                                    type="checkbox"
                                    className="form-check-input"
                                    checked={this.state.MasterChecked}
                                    id="mastercheck"
                                   onChange={(e) => this.onMasterCheck(e)}
                                />
                            </th>
                            <th scope="col"> Название </th>
                            <th scope="col"> Автор </th>
                            <th scope="col"> Iban </th>
                        </tr>
                        </thead>
                        <tbody>
                        {
                            this.state.books.map(
                                book =>
                                    <tr key = {book.id} className={book.selected ? "selected" : ""} onDoubleClick ={()=>this.rowhandler(book.id)}>
                                        <th scope="row">
                                            <input
                                                type="checkbox"
                                                checked={book.selected}
                                                className="form-check-input"
                                                id="rowcheck{book.id}"
                                               onChange={(e) => this.onItemCheck(e, book)}
                                            />
                                        </th>
                                        <td> {book.name}  </td>
                                        <td> {book.author} </td>
                                        <td> {book.iban} </td>

                                    </tr>
                            )
                        }
                        </tbody>
                    </table>

                    {/*<div className="row">*/}
                    {/*    <b>All Row Items:</b>*/}
                    {/*    <code>{JSON.stringify(this.state.List)}</code>*/}
                    {/*</div>*/}
                    {/*<div className="row">*/}
                    {/*    <b>Selected Row Items(Click Button To Get):</b>*/}
                    {/*    <code>{JSON.stringify(this.state.SelectedList)}</code>*/}
                    {/*</div>*/}




                </div>
               <div style={{float: 'right',marginLeft:'5px'}}>
                <button
                    className="btn btn-primary"
                    onClick={this.deleteBooks.bind(this)}
                >
                    Удалить
                </button>

               </div>
                <div style={{float: 'right'}}>
                <button
                  className="btn btn-primary"
                   onClick={this.addBook.bind(this)}
                 >
                    Добавить
               </button>
            </div>

    </div>


        );
    }
}

export default ListBookComponent;

