import './App.css';
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import ListBookComponent from "./components/ListBookComponent";
import HeaderComponent from "./components/HeaderComponent";
import FooterComponent from "./components/FooterComponent";
import AddBookComponent from "./components/AddBookComponent";
import SearchBookByIdComponent from "./components/GetBookByIdComponent";
import GetBookByIdComponent from "./components/GetBookByIdComponent";
import UpdateBookComponent from "./components/UpdateBookComponent";
import GetBookByAuthorComponent from "./components/GetBookByAuthorComponent";
import LoginComponent from "./components/LoginComponent";

function App() {
  return (
      <div>
          <Router>
              <HeaderComponent/>
              <div className="container">
                  <Switch>
                      <Route path="/add-book" component={AddBookComponent}/>
                      <Route path="/login" component={LoginComponent}/>
                      <Route path="/search-book-id/:id" component={GetBookByIdComponent}/>
                      <Route path="/search-book-author/:author" component={GetBookByAuthorComponent}/>
                      <Route path="/update-book/:id" component={UpdateBookComponent}/>
                      <Route path="/" exact component={ListBookComponent}/>
                  </Switch>
              </div>
              <FooterComponent/>
          </Router>

      </div>
  );
}

export default App;
