import axios from "axios";

// константа для endpoint
const BOOKS_API_URL = "http://localhost:8080/api/v1/books"

const BOOK_BY_ID_API_URL = "http://localhost:8080/api/v1/book"

const USER_BOOK_ID_API_URL = "http://localhost:8080/api/v2/user-book"

const BOOKS_BY_AUTHOR_API_URL = "http://localhost:8080/api/v1/books/author"


class BookService {
    id;
    selectedRows;
    userId;
    author;

    setId(id) {
        this.id = id
    }

    setAuthor(author) {
        this.author = author
    }


    getBooks() {
        return axios.get(BOOKS_API_URL);
    }

    // метод создания книги
    addBook(book) {
        return axios.post(BOOKS_API_URL, book, {
            headers:{
                'content-type': 'application/json'
            }
        });
    }

    getBookById(id) {
        this.id = id;
        return axios.get(BOOK_BY_ID_API_URL + '/' + id, {})
    }
    getBookByAuthor(author) {
        this.author = author;
        return axios.get(BOOKS_BY_AUTHOR_API_URL + '/' + author, {})
    }


    async deleteBook(selectedRows) {
        this.selectedRows = selectedRows;
        console.log('selectedRows => ' + JSON.stringify(selectedRows));
        for (const rowId of selectedRows) {
            console.log('rowId => ' + rowId);

            await axios.delete(BOOK_BY_ID_API_URL + '/' + rowId, {})
        }
    }

    getUserBook(userId) {
        this.userId = userId;
        return axios.get(USER_BOOK_ID_API_URL + '/' + userId, {})
    }

    // async addUserBook(userBook) {
    //     this.selectedRows = selectedRows;
    //     console.log('selectedRows => ' + JSON.stringify(selectedRows));
    //     for (const rowId of selectedRows) {
    //         console.log('rowId => ' + rowId);
    //         await axios.post(USER_BOOK_ID_API_URL, userBook)
    //     }
    // }

    async deleteUserBook(selectedRows) {
        this.selectedRows = selectedRows;
        console.log('selectedRows => ' + JSON.stringify(selectedRows));
        for (const rowId of selectedRows) {
            console.log('rowId => ' + rowId);

            await axios.delete(USER_BOOK_ID_API_URL + '/' + rowId, {})
        }
    }
    updateBookById(id, book) {
        this.id = id;
        return axios.put(BOOK_BY_ID_API_URL + '/' + id, book)
    }

}
export default new BookService()