import React, {Component} from 'react';
import BookService from "../services/BookService";

class GetBookByIdComponent extends Component {

    constructor(props) {
        super(props);

        this.state = {
            book: {}
        }
    }

    // присваиваем значения состоянию компонента
    componentDidMount() {
        const { id } = this.props.match.params
        console.log(id )
        BookService.setId(id)

        BookService.getBookById(BookService.id).then((res) => {
            this.setState({book: res.data});
        });
    }
    returnToList(){

        this.props.history.push('/');
        window.location.reload()
    }


    render() {
        const  book = this.state.book
        return (
            <div style={{marginBottom: '50px'}}>
                <h2 className="text-center"> Найденная книга </h2>
                <div className="row">

                    <table className="table table-striped table-bordered">
                        <thead>
                        <tr>
                            <th> Название </th>
                            <th> Автор </th>
                            <th> Iban </th>
                        </tr>
                        </thead>
                        <tbody>
                                    <tr key = {book.id}>
                                        <td> {book.name} </td>
                                        <td> {book.author} </td>
                                        <td> {book.iban} </td>
                                    </tr>
                        </tbody>
                    </table>

                </div>
                <div style={{float: 'right',marginLeft:'5px',marginTop: '5px',marginBottom: '5px'}}>
                    <button
                        className="btn btn-primary"
                        onClick={this.returnToList.bind(this)}>Сбросить
                    </button>
                </div>
            </div>
        );
    }
}

export default GetBookByIdComponent;