import React, {Component} from 'react';
import BookService from "../services/BookService";

class AddBookComponent extends Component {
    constructor(props) {
        super(props);

        // объявляем состояния приложения
        this.state = {
            name: '',
            author: '',
            iban: ''
        }

        // через методы присваиваем значения состоянию
        this.initNameHandler = this.initNameHandler.bind(this);
        this.initAuthorHandler = this.initAuthorHandler.bind(this);
        this.initIbanHandler = this.initIbanHandler.bind(this);
        this.saveBook = this.saveBook.bind(this);

    }

// создаём методы для присвоения значения состоянию
    initNameHandler = (event) => {
        this.setState({name: event.target.value});
    }

    initAuthorHandler = (event) => {
        this.setState({author: event.target.value});
    }

    initIbanHandler = (event) => {
        this.setState({iban: event.target.value});
    }

    saveBook = (e) => {
        e.preventDefault();
        let book = {name: this.state.name, author: this.state.author, iban: this.state.iban}
        //console.log('employee => ' + JSON.stringify(employee));

        if(book.name!=null && book.author!=null) {
            BookService.addBook(book).then(res => {
                this.props.history.push('/');
                window.location.reload()
            });
        }
    }

    // метод для отмены создания работника и перехода на список работников
    cancel() {
       this.props.history.push('/');
        window.location.reload()
    }

    // метод, отвечающий за отображение на странице браузера
    render() {
        return (
            <div style={{marginBottom:'10px', marginTop: '10px'}}>
                <div className="container">
                    <div className="row">
                        <div className="card col-md-6 offset-md-3 offset-md-3">
                            <h3 className="text-center"> Добавить книгу </h3>
                            <div className="card-body">

                                <form>
                                    <div className="form-group">
                                        <label> Название </label>
                                        <input
                                            placeholder="Название"
                                            name="name"
                                            className="form-control"
                                            value={this.state.name}
                                            onChange={this.initNameHandler}/>
                                    </div>
                                    <div className="form-group">
                                        <label> Автор </label>
                                        <input
                                            placeholder="Автор"
                                            name="author"
                                            className="form-control"
                                            value={this.state.author}
                                            onChange={this.initAuthorHandler}/>
                                    </div>
                                    <div className="form-group">
                                        <label> Iban </label>
                                        <input
                                            placeholder="Iban"
                                            name="iban"
                                            className="form-control"
                                            value={this.state.iban}
                                            onChange={this.initIbanHandler}/>
                                    </div>
                                    <div style={{float: 'right',marginLeft:'5px',marginTop: '5px'}}>
                                    <button
                                        className="btn btn-success"
                                        onClick={this.saveBook.bind(this)}>Сохранить
                                    </button>
                                    </div>
                                    <div style={{float: 'right',marginLeft:'5px',marginTop: '5px'}}>
                                    <button
                                        className="btn btn-danger"
                                        onClick={this.cancel.bind(this)}>Отменить
                                    </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default AddBookComponent;

